        <!-- Proposal Submission Form -->
        <div class="bg-white rounded-lg shadow-sm border p-6">
          <h3 class="text-lg font-semibold text-gray-900 mb-4">Proposal Submission</h3>
          
          <form @submit.prevent="submitProposal" class="space-y-6">
            <!-- Technical Proposal -->
            <div>
              <label class="block text-sm font-medium text-gray-700 mb-2">Technical Proposal</label>
              <textarea 
                v-model="proposalData.technical_proposal"
