              ></textarea>
            </div>

            <!-- Commercial Proposal -->
            <div>
              <label class="block text-sm font-medium text-gray-700 mb-2">Commercial Proposal</label>
              <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label class="block text-sm font-medium text-gray-700 mb-1">Proposed Value ($)</label>
                  <input 
                    type="number" 
