                  <select 
                    v-model="proposalData.payment_terms"
                    class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="">Select payment terms</option>
                    <option value="net_30">Net 30</option>
                    <option value="net_60">Net 60</option>
                    <option value="upfront">Upfront</option>
                    <option value="milestone">Milestone-based</option>
                  </select>
                </div>
              </div>
            </div>

            <!-- Timeline -->
            <div>
              <label class="block text-sm font-medium text-gray-700 mb-2">Project Timeline</label>
              <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label class="block text-sm font-medium text-gray-700 mb-1">Start Date</label>
                  <input 
                    type="date" 
                    v-model="proposalData.start_date"
                    class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  />
                </div>
                <div>
                  <label class="block text-sm font-medium text-gray-700 mb-1">Completion Date</label>
                  <input 
                    type="date" 
                    v-model="proposalData.completion_date"
                    class="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  />
                </div>
              </div>
            </div>

            <!-- Document Upload -->
            <div>
              <label class="block text-sm font-medium text-gray-700 mb-2">Supporting Documents</label>
              <div class="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center">
                <input 
                  type="file" 
                  ref="fileInput"
                  @change="handleFileUpload"
                  multiple
                  accept=".pdf,.doc,.docx,.xls,.xlsx,.ppt,.pptx"
                  class="hidden"
                />
                <button 
                  type="button"
                  @click="$refs.fileInput.click()"
