                >
                  Choose Files
                </button>
                <p class="text-sm text-gray-500 mt-2">PDF, DOC, DOCX, XLS, XLSX, PPT, PPTX files accepted</p>
              </div>
              
              <!-- Uploaded Files List -->
              <div v-if="uploadedFiles.length > 0" class="mt-4 space-y-2">
                <h4 class="text-sm font-medium text-gray-700">Uploaded Files:</h4>
                <div v-for="(file, index) in uploadedFiles" :key="index" class="flex items-center justify-between p-2 bg-gray-50 rounded">
                  <span class="text-sm text-gray-700">{{ file.name }}</span>
                  <button 
                    type="button"
                    @click="removeFile(index)"
