                placeholder="Any additional information, clarifications, or special considerations..."
              ></textarea>
            </div>

            <!-- Submit Buttons -->
            <div class="flex justify-between items-center pt-6 border-t">
              <button 
                type="button"
                @click="saveDraft"
                :disabled="submitting"
                class="px-6 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:opacity-50"
              >
                Save Draft
              </button>
              <button 
                type="submit"
                :disabled="submitting"
                class="px-6 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:opacity-50"
              >
                <span v-if="submitting">Submitting...</span>
