</template>

<script setup>
import { ref, onMounted } from 'vue'
import { useRoute } from 'vue-router'
import rfpButton from '@/components/rfpButton.vue'
const vendorInvitationService = require('@/services/vendorInvitationService')
const rfpResponseService = require('@/services/rfpResponseService')

const route = useRoute()
const loading = ref(true)
const error = ref(null)
const submitting = ref(false)

const vendorInfo = ref({
  vendor_name: '',
  vendor_email: '',
  vendor_phone: '',
  company_name: ''
})

const rfpData = ref({
  title: '',
  description: '',
  deadline: '',
  budget_min: 0,
  budget_max: 0
})

const rfpDocuments = ref([
  {
    id: 1,
    name: 'RFP Requirements Document',
    size: '2.4 MB',
    type: 'PDF',
    url: '/documents/rfp-requirements.pdf'
  },
  {
    id: 2,
    name: 'Technical Specifications',
    size: '1.8 MB',
    type: 'PDF',
    url: '/documents/technical-specs.pdf'
  },
  {
    id: 3,
    name: 'Evaluation Criteria',
    size: '856 KB',
    type: 'PDF',
    url: '/documents/evaluation-criteria.pdf'
  }
])

const proposalData = ref({
  technical_proposal: '',
  proposed_value: '',
  payment_terms: '',
  start_date: '',
  completion_date: '',
  additional_info: ''
})

const uploadedFiles = ref([])

const loadInvitationData = async () => {
  try {
    const token = route.params.token
    if (!token) {
      error.value = 'Invalid invitation token'
      return
    }

    const response = await vendorInvitationService.getInvitationByToken(token)
    vendorInfo.value = response.invitation
    
    // Load RFP data (this would come from a separate API call)
    rfpData.value = {
      title: 'Software Development RFP',
      description: 'We are seeking a qualified software development company to build a comprehensive web application...',
      deadline: '2025-02-15',
      budget_min: 50000,
      budget_max: 150000
    }
    
  } catch (err) {
    console.error('Error loading invitation data:', err)
    error.value = 'Failed to load invitation data. Please check your link and try again.'
  } finally {
    loading.value = false
  }
}

const formatDate = (dateString) => {
  return new Date(dateString).toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  })
}

const downloadDocument = (document) => {
  // In a real application, this would trigger a download
  console.log('Downloading document:', document.name)
  // For demo purposes, we'll just show an alert
  alert(`Downloading ${document.name}`)
}

const handleFileUpload = (event) => {
  const files = Array.from(event.target.files)
  files.forEach(file => {
    uploadedFiles.value.push({
      name: file.name,
      size: file.size,
      type: file.type,
      file: file
    })
  })
}

const removeFile = (index) => {
  uploadedFiles.value.splice(index, 1)
}

const saveDraft = async () => {
  try {
    submitting.value = true
    const token = route.params.token
    
    const draftData = {
      ...proposalData.value,
      files: uploadedFiles.value,
      completion_percentage: calculateCompletionPercentage()
    }
    
    await rfpResponseService.saveDraft(token, draftData)
    alert('Draft saved successfully!')
  } catch (err) {
    console.error('Error saving draft:', err)
    alert('Failed to save draft')
  } finally {
    submitting.value = false
  }
}

const submitProposal = async () => {
  try {
    submitting.value = true
    const token = route.params.token
    
    // Validate required fields
    if (!proposalData.value.technical_proposal || !proposalData.value.proposed_value) {
      alert('Please fill in all required fields')
      return
    }
    
    const responseData = {
      technical_proposal: proposalData.value.technical_proposal,
      proposed_value: parseFloat(proposalData.value.proposed_value),
      payment_terms: proposalData.value.payment_terms,
      start_date: proposalData.value.start_date,
      completion_date: proposalData.value.completion_date,
      additional_info: proposalData.value.additional_info,
      documents: uploadedFiles.value.map(file => ({
        name: file.name,
        size: file.size,
        type: file.type
      })),
      completion_percentage: 100.00,
      submitted_by: vendorInfo.value.vendor_name
    }
    
    await rfpResponseService.submitResponse(token, responseData)
    alert('Proposal submitted successfully!')
    
    // Redirect or show success message
  } catch (err) {
    console.error('Error submitting proposal:', err)
    alert('Failed to submit proposal')
  } finally {
    submitting.value = false
  }
}

const calculateCompletionPercentage = () => {
  let completed = 0
  let total = 6 // Total number of fields
  
  if (proposalData.value.technical_proposal) completed++
  if (proposalData.value.proposed_value) completed++
  if (proposalData.value.payment_terms) completed++
  if (proposalData.value.start_date) completed++
  if (proposalData.value.completion_date) completed++
  if (proposalData.value.additional_info) completed++
  
  return Math.round((completed / total) * 100)
}

onMounted(() => {
  loadInvitationData()
})
</script>

